import { Button } from "@/components/ui/button";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Label } from "@radix-ui/react-label";
import { RadioGroup, RadioGroupItem } from "@radix-ui/react-radio-group";
import React from "react";

const index = () => {

  const bookingOptions = [
    {
      id: "single",
      value: "single",
      title: "Single-Slot Booking",
      description: "The radiographer operates one scanner for you, but may manage up to 2 more for other organizations simultaneously",
    },
    {
      id: "exclusive",
      value: "exclusive",
      title: "Exclusive Booking",
      description: "The radiographer works only for your organization, operating up to 3 scanners simultaneously as needed",
    }
  ];

    // Handler for radio selection change
  const handleValueChange = (value: string) => {
    // Find the selected option
    const selectedOption = bookingOptions.find(option => option.value === value);
    
    // Show alert
    alert(`You selected: ${selectedOption?.title}`);
    
    // Alternative: Using ShadCN toast (more elegant)
    // toast({
    //   title: "Selection Updated",
    //   description: `You selected: ${selectedOption?.title}`,
    // });
  };

  return (
   <Card className="w-full max-w-md">
      <CardHeader>
        <CardTitle className="text-lg">What type of booking do you require?</CardTitle>
      </CardHeader>
      <CardContent>
        <RadioGroup 
          defaultValue="exclusive" 
          className="space-y-4"
          onValueChange={handleValueChange} // Add this handler
        >
          {bookingOptions.map((option) => (
            <div key={option.id} className="flex items-center space-x-3 space-y-0">
              <RadioGroupItem 
                value={option.value} 
                id={option.id}
              />
              <Label htmlFor={option.id} className="flex flex-col space-y-1">
                <span className="font-medium">{option.title}</span>
                <span className="text-sm text-muted-foreground font-normal">
                  {option.description}
                </span>
              </Label>
            </div>
          ))}
        </RadioGroup>

        <div className="flex justify-between mt-8">
          <Button variant="outline">Cancel</Button>
          <Button>Next â†’</Button>
        </div>
      </CardContent>
    </Card>
  );
};

export default index;
